@import '../Base/index'

.#{$PREFIX + "PageIndex"}
  > .msg
    position: absolute
    width: 500px
    left: calc(50% - 250px)
    z-index: 9

    &.error
      background-color: $red
    &.success
      background-color: $green

    color: white
    text-align: center
    height: 26px
    line-height: 26px
    font-size: 12px
    font-family: monospace
    border-radius: 8px
    top: 10px
    > .close
      position: absolute
      right: 10px
      top: 0
      font-size: 20px
      user-select: none
      cursor: pointer
      &:hover
        color: #ccc

  > .wrapper
    > .upload
      padding: 20px 0
      box-sizing: border-box
    > .content
      overflow: hidden
      box-sizing: border-box
      > .item
        float: left
        width: 100px
        height: 120px
        border: 1px solid #ccc
        margin: 15px
        cursor: pointer
        position: relative
        user-select: none
        border-radius: 4px
        &.upload
          > .layer
            opacity: 1
        > .layer
          font-size: 12px
          position: absolute
          left: 0
          top: 0
          width: 100%
          height: 100%
          background: rgba(0, 0, 0, 0.5)
          opacity: 0
          &:hover
            opacity: 1
          > .text
            position: absolute
            width: 100%
            height: 25px
            line-height: 25px
            bottom: 0
            text-align: center
            color: white
            background: rgba(0, 128, 0, 0.8)


        > .img
          width: 100%
          height: 100%
          background-repeat: no-repeat
          background-size: contain
          background-position: center center
